import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default{
	components:{
		uniLoadMore
	},
	data() {
		return {
			uid:'',
			token:'',
			sh:'',
			dataList:[],
			page:1,
			pageSize:20,
			total:0,
			more:''
		}
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	   mounted(){
		   this.init()
	   },
	   methods:{
		   init(){
			   this.reset()
			   var userInfo=uni.getStorageSync('userInfo')
			      if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			      	this.uid=userInfo.uid
			      	this.token=userInfo.token
			      	this.getList()
			      }
			   var that=this
			   setTimeout(function(){
			   	that.$getHeight('#sv',(res) =>{
			   		that.sh=res
			   	})
			   },0)
		   },
		   reset(){
		   	this.page=1
		   	this.total=0
		   	this.dataList=[]
		   	this.more=''
		   },
		   getList(p){
		   	var that=this
		   	var params={
				uid:this.uid,
				token:this.token,
		   	  page:p,
		   	  pagesize: this.pageSize
		   	}
		   	if(this.page==1){
		   		this.$loading()
		   	}
		   	  var url='&r=api.myshop.members'
		   	  this.$apiPost(url,params).then((res) =>{
		   		  that.total=res.total
		   		  that.dataList=that.dataList.concat(res.data)
		   		  that.more=''
		   		  if(that.page==1){
		   		  	uni.hideLoading()
		   		  }
		   	  })
		   },
		   toBottom(){
		   	if(this.noMore){
		   		this.more='noMore'
		   		return;
		   	}
		   	var that=this
		   	this.more='loading'
		     // setTimeout(function(){
		   	  that.page++
		   	  that.getList(that.page)
		     // },2000)
		   },
		   remove(id,where){
			   this.$loading()
			   var that=this
			   var params={
			   	uid:this.uid,
			   	token:this.token,
			     fansUid: id
			   }
			     var url='&r=api.myshop.members.remove'
			     this.$apiPost(url,params).then((res) =>{
			   	  uni.hideLoading()
				  that.$msg('移除成功')
				  that.dataList.splice(where,1)
			     })
		   }
	   }
}
